import "./Hero.css";
import React, {
  lazy,
  Suspense,
  useRef,
  useEffect,
  useState,
  useMemo,
} from "react";

/* SWIPER */
import { Swiper, SwiperSlide } from "swiper/react";
import { Keyboard } from "swiper/modules";
import "swiper/css";
import "swiper/css/pagination";

/* PRELOADER */
import { Loader } from "../Loader/Loader";
import {
  BrowserView,
  MobileView,
  isBrowser,
  isMobile,
} from "react-device-detect";

// FRAMER MOTION //
import {
  motion,
  useScroll,
  useTransform,
  LazyMotion,
  domAnimation,
  AnimatePresence,
} from "framer-motion";

import { Link, useLocation } from "react-router-dom";

function Hero({
  handlePlayerChange,
  player,
  initial,
  fromBottom,
  resetFromBottom,
}) {
  const location = useLocation();

  const [playerSelected, SetPlayerSelected] = useState(player);
  const [internalCall, setInternalCall] = useState(false);

  useEffect(() => {
    if(internalCall){
      handlePlayerChange(playerSelected);
    }
  }, [playerSelected, internalCall]); // eslint-disable-line

  useEffect(() => {
    SetPlayerSelected(player);
    _changeItem(player);
  }, [player]); // eslint-disable-line

  useEffect(() => {
    if(location.search == '?gohome=true'){
      //updatePlayer();
    }
  }, [location.search])

  /////////////
  // PLAYERS //
  /////////////
  const headerSlides = [
    {
      player: "https://prod.spline.design/q6TuxNKv17Rnj8nQ/scene.splinecode",
      //player: process.env.PUBLIC_URL + "/images/players/torus.spline",
      playerName: "Futuristic",
      playerPNG: "images/players/abstract.png",
      icon: "symbol",
      backGradient:
        "linear-gradient(116deg, rgba(164, 179, 255, 0.40) 1.25%, rgba(189, 203, 255, 0.40) 29.79%, rgba(206, 184, 255, 0.40) 71.6%, rgba(204, 167, 255, 0.40) 100%)",
      playerShadow:
        "radial-gradient(ellipse at 50% 50%,rgba(128, 158, 255,.8),transparent,transparent)",
      text: "/Prompt: <b>Futuristic</b>",
    },
    {
      player: "https://prod.spline.design/FQl-eHlCtJ21iYP6/scene.splinecode",
      //player: process.env.PUBLIC_URL + "/images/players/shiba.spline",
      playerName: "Shiba",
      playerPNG: "images/players/shiba.png",
      icon: "dog",
      backGradient:
        "linear-gradient(116deg,#ffde9f 1.25%,#fff2d0 29.54%,#ffe3cf 65.02%,#ffb7a0 100%)",
      playerShadow:
        "radial-gradient(ellipse at 50% 50%,rgba(253, 153, 153, .8),transparent,transparent)",
      text: "/Prompt: <b>Dog(e)-Mode</b>",
    },
    {
      player: "https://prod.spline.design/yebHJKvb7znHU-HM/scene.splinecode",
      //player: process.env.PUBLIC_URL + "/images/players/quill.spline",
      playerName: "Quill",
      playerPNG: "images/players/quill.png",
      icon: "quill",
      backGradient:
        "linear-gradient(116deg, #DDD3F1 1.25%, #EEEDFF 31.59%, #FFDBE1 71.71%, #FBB 100%)",
      playerShadow:
        "radial-gradient(ellipse at 50% 50%,rgba(204, 177, 232, .8),transparent,transparent)",
      text: "/Prompt: <b>Shakespearinator</b>",
    },
    {
      player: "https://prod.spline.design/GXxlPer7HcMkmnyj/scene.splinecode",
      //player: process.env.PUBLIC_URL + "/images/players/guitar.spline",
      playerName: "Guitar",
      playerPNG: "images/players/guitar.png",
      icon: "guitar",
      backGradient:
        "linear-gradient(116deg, #FFF2D0 1.25%, #F1FFD4 33.14%, #E1FFE6 65.02%, #D4FFF5 100%)",
      playerShadow:
        "radial-gradient(ellipse at 50% 50%,rgba(123, 149, 217, .8),transparent,transparent)",
      text: "/Prompt: <b>Heavy Metal-ic</b>",
    },
    {
      player: "https://prod.spline.design/J4eVrTx9EGedijva/scene.splinecode",
      //player: process.env.PUBLIC_URL + "/images/players/disco.spline",
      playerName: "Disco",
      playerPNG: "images/players/disco.png",
      icon: "sphere",
      backGradient:
        "linear-gradient(116deg, rgba(129, 218, 202, 0.40) 1.25%, rgba(118, 228, 202, 0.40) 29.02%, rgba(149, 236, 255, 0.40) 67.6%, rgba(149, 147, 255, 0.40) 100%)",
      playerShadow:
        "radial-gradient(ellipse at 50% 50%,rgba(190, 152, 238, .8),transparent,transparent)",
      text: "/Prompt: <b>Gen-Zify</b>",
    },
  ];

  ////////////
  // SLIDER //
  ////////////
  const [swiper, setSwiper] = useState(null);
  const sliderRef = useRef();
  const nexto = () => {
    swiper.slideNext();
    setInternalCall(true);
    setTimeout(() => {
      setInternalCall(false);
    }, 2000);
  };
  const prevto = () => {
    swiper.slidePrev();
    setInternalCall(true);
    setTimeout(() => {
      setInternalCall(false);
    }, 2000);
  };
  const _changeItem = (i) => {
    sliderRef.current.swiper.slideToLoop(i);
  };

  /// PRELOAD PLAYERS ///
  /*const [playersLoaded, setPlayersLoaded] = useState(false);

  useEffect(() => {
    const loadImage = (player) => {
      return new Promise((resolve, reject) => {
        const loadPlayer = new Spline(); 
        loadPlayer.scene = player.player;
        // wait 2 seconds to simulate loading time
        loadPlayer.onload = () =>
          setTimeout(() => {
            resolve(player.player);
          }, 2000);

          loadPlayer.onerror = (err) => reject(err);
      });
    };

    Promise.all(headerSlides.map((player) => loadImage(player)))
      .then(() => setPlayersLoaded(true))
      .catch((err) => console.log("Failed to load images", err));
  }, []);*/

  ///////////////////
  ///////////////////

  // FRAMER SCROLL ANIMATIONS //
  const ref = useRef(null);
  const { scrollYProgress } = useScroll({
    offset: ["0 0", "0.1 1"],
  });
  const opacityProgress = useTransform(scrollYProgress, [0, 1], [0, 1]);
  const opacityControlsProgress = useTransform(scrollYProgress, [0, 1], [1, 0]);
  const opacityControlsArrowProgress = useTransform(
    scrollYProgress,
    [0, 0.2],
    [1, 0]
  );

  const [progressEndValue, setProgressEndValue] = useState(0.45);
  useEffect(() => {
    const handleResize = () => {
      if (window.innerWidth <= 480) {
        setProgressEndValue(0.8);
      } else {
        setProgressEndValue(0.45);
      }
    };

    handleResize();
    window.addEventListener("resize", handleResize);

    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const [progressScrollValue, setProgressScrollValue] = useState(140);
  useEffect(() => {
    const handleResize = () => {
      if (window.innerHeight <= 430) {
        setProgressScrollValue(0);
      } else {
        setProgressScrollValue(140);
      }
    };
    handleResize();
    window.addEventListener("resize", handleResize);
    return () => {
      window.removeEventListener("resize", handleResize);
    };
  }, []);

  const scaleProgress = useTransform(
    scrollYProgress,
    [0, 1],
    [1, progressEndValue]
  );
  const scaleTopProgress = useTransform(
    scrollYProgress,
    [0, 1],
    [0, progressScrollValue]
  );
  const topProgress = useTransform(scrollYProgress, [0, 1], [0, -1000]);

  /// PRELOADER ///
  const [isLoading, setIsLoading] = useState(true);
  const [playerLoaded, setPlayerLoaded] = useState(false);
  useEffect(() => {
    const fakeDataFetch = () => {
      setTimeout(() => {
        setIsLoading(false);
      }, 4000);
    };
    fakeDataFetch();
  }, []);

  return (
    <section className="section_hero">
      {isLoading ? <Loader /> : null}

      <div className="header-players" ref={ref}>
        <motion.div
          className="players-slider"
          animate={{
            background: headerSlides[playerSelected].backGradient,
          }}
          transition={{ duration: 0.5 }}
        >
          <div className="players-slider_texts">
            <motion.div
              className="players-slider_title"
              style={{ scale: scaleProgress, top: scaleTopProgress }}
            >
              Play
            </motion.div>
            <motion.div
              className="players-slider_subtitle"
              style={{ opacity: opacityProgress }}
            >
              An AI Exploration
            </motion.div>
          </div>
          <motion.div
            className="player-shadow"
            style={{ opacity: opacityControlsProgress }}
            animate={{
              background: headerSlides[playerSelected].playerShadow,
            }}
            transition={{ duration: 0.5 }}
          ></motion.div>

          <motion.div
            className="spline_container"
            style={{
              width: "100%",
              height: "100%",
              position: "absolute",
              top: topProgress,
              opacity: opacityControlsProgress,
            }}
          >
            <Swiper
              slidesPerView={1}
              spaceBetween={0}
              initialSlide={0}
              loop={true}
              centeredSlides={true}
              onSwiper={(s) => {
                setSwiper(s);
                SetPlayerSelected(s.realIndex);
              }}
              onTransitionEnd={(s) => {
                SetPlayerSelected(s.realIndex);
              }}
              onRealIndexChange={(s) => {}}
              keyboard={{
                enabled: true,
              }}
              modules={[Keyboard]}
              className="hero-swiper"
              ref={sliderRef}
              navigation={{
                nextEl: "image-swiper-button-next",
                prevEl: "image-swiper-button-prev",
                disabledClass: "swiper-button-disabled",
              }}
            >
              {headerSlides.map((data, index) => (
                <SwiperSlide
                  key={index}
                  onClick={(event) => {
                    if (
                      !event.currentTarget.classList.contains(
                        "swiper-slide-active"
                      )
                    ) {
                      if (
                        event.currentTarget.classList.contains(
                          "swiper-slide-next"
                        )
                      ) {
                        nexto();
                      } else {
                        prevto();
                      }
                    }
                  }}
                  className={playerSelected === index ? data.icon : ""}
                >
                  <div
                    style={{
                      position: "absolute",
                      width: "100%",
                      height: "100%",
                      display: "flex",
                      justifyContent: "center",
                      alignItems: "center",
                    }}
                  >
                    <img src={data.playerPNG} />
                  </div>
                </SwiperSlide>
              ))}
            </Swiper>
          </motion.div>

          {/* PROMT */}
          <motion.div
            className="navigation"
            style={{ opacity: opacityControlsProgress }}
          >
            <h2 className="promt-title">
              Change your experience with each prompt{" "}
            </h2>
            <p
              className="prompt"
              dangerouslySetInnerHTML={{
                __html: headerSlides[playerSelected].text,
              }}
            ></p>
          </motion.div>

          {/*// NAVIGATION //*/}
          <motion.div
            className="swiper-button image-swiper-button-next"
            style={{
              opacity: opacityControlsArrowProgress,
            }}
            onClick={() => {
              nexto();
            }}
          />
          <motion.div
            className="swiper-button image-swiper-button-prev"
            style={{
              opacity: opacityControlsArrowProgress,
            }}
            onClick={() => {
              prevto();
            }}
          />
        </motion.div>
      </div>
    </section>
  );
}

export default Hero;
